<template>
    <div class="container">
        <div class="title">各风险类别占比及货币数量</div>
        <Echart :options="options" height="320px" width="500px" />
    </div>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {
                color: ['#50BA89', '#E61958', '#F5B51A', '#79CBDB', '#2A51A3'],
                legend: {
                    show: false,
                },
                series: [
                    {
                        type: 'pie',
                        radius: [0, 120],
                        center: ['50%', '50%'],
                        roseType: 'area',
                        itemStyle: {
                            borderRadius: 8,
                        },
                        label: {
                            show: true,
                            normal: {
                                formatter: '{c}%',
                                color: 'white',
                                fontSize: '16',
                            },
                        },
                        labelLine: {
                            show: true,
                        },
                        emphasis: {
                            label: {
                                show: true,
                            },
                        },
                        data: [
                            { value: 30, name: '湖北省' },
                            { value: 35, name: '湖南省' },
                            { value: 40, name: '北京市' },
                            { value: 30, name: '四川省' },
                            { value: 28, name: '其它' },
                        ],
                    },
                ],
            },
        }
    },
    components: {
        Echart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 5) - 1
                if (tmp1 >= 0) {
                    let tmp2 = tmp1 == 0 ? 4 : tmp1 - 1
                    let options = JSON.parse(JSON.stringify(this.options))
                    let num = options.series[0].data[tmp1].value
                    let num2 = options.series[0].data[tmp2].value
                    options.series[0].data[tmp1].value = num + 1
                    options.series[0].data[tmp2].value = num2 - 1

                    this.options = options
                }
            }, 2000)
        },
    },
}
</script>

